:root {
  --darkest: #221a0f;
  --gray: #414042;
  --asset-width: 750px;
  --asset-height: 1050px;
  --asset-font-size: 2.2em;
}

.asset {
  font-family: "PT Serif";
  color: #221a0f;
  color: var(--darkest);
  background-color: #fff;
  background-image: url(/ironsworn-asset-workbench/static/media/ironsworn_asset_background.99161ece.svg);
  background-repeat: no-repeat;
  background-size: contain;
  box-sizing: border-box;
  font-size: 2.2em;
  font-size: var(--asset-font-size);
  height: 1050px;
  height: var(--asset-height);
  width: 750px;
  width: var(--asset-width);
  line-height: 1.3em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.top {
  text-transform: uppercase;
  font-weight: 900;
  font-family: Simonetta;
}

.type {
  color: white;
  padding: 0.8em 0 0.3em 0.95em;
  font-size: 1.03em;
}

.asset-name {
  font-size: 1.26em;
  padding: 0.7em 0.7em 0.53em 0.7em;
}

.header-circle {
  color: white;
  border: 0.19em solid transparent;
  border-radius: 50%;
  width: 4em;
  height: 4em;
  float: right;
  margin-top: -1.8em;
  margin-right: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-icon {
  font-size: 2.5em;
}

.details {
  margin-left: 0.9em;
  margin-right: 0.7em;
  font-size: 0.97em;
}

.write-in {
  border-bottom: 0.14em solid #414042;
  border-bottom: 0.14em solid var(--gray);
  margin-right: 0.2em;
  margin-bottom: 0.71em;
}

.write-in::after {
  content: ":";
}

.description {
  margin-bottom: 0.28em;
}

.ability {
  display: flex;
  line-height: 1.36em;
}

.ability-name {
  font-weight: bold;
}

.ability-name::after {
  content: ": ";
}

.dot {
  margin: -0.06em 0.27em 0 -0.03em;
  font-size: 1.58em;
}

.filled::before {
  content: "●";
}

.unfilled::before {
  content: "○";
}

li {
  list-style-position: outside;
  margin-left: 1em;
}

.track {
  display: flex;
  justify-content: center;
  margin-bottom: 0.85em;
  font-size: 1.42em;
  font-family: Simonetta;
  color: #414042;
  color: var(--gray);
  width: 91.5%;
  align-self: center;
}

.value.text {
  padding: 0.7em;
  text-align: center;
}

.value {
  width: 100%;
  height: 1.7em;
  border: 0.1em solid #414042;
  border: 0.1em solid var(--gray);
  border-left: 0.05em solid #414042;
  border-left: 0.05em solid var(--gray);
  font-weight: bolder;
  display: flex;
  justify-content: center;
  align-items: center;
}

.value svg {
  width: 100%;
  height: auto;
}

.value:first-child {
  border-left: 0.1em solid #414042;
  border-left: 0.1em solid var(--gray);
}

.asset.two-thirds {
  height: calc(1050px * (2 / 3));
  height: calc(var(--asset-height) * (2 / 3));
  width: calc(750px * (2 / 3));
  width: calc(var(--asset-width) * (2 / 3));
  font-size: calc(2.2em * (2 / 3));
  font-size: calc(var(--asset-font-size) * (2 / 3));
}

.asset.one-half {
  height: calc(1050px / 2);
  height: calc(var(--asset-height) / 2);
  width: calc(750px / 2);
  width: calc(var(--asset-width) / 2);
  font-size: calc(2.2em / 2);
  font-size: calc(var(--asset-font-size) / 2);
}

.asset.one-third {
  height: calc(1050px / 3);
  height: calc(var(--asset-height) / 3);
  width: calc(750px / 3);
  width: calc(var(--asset-width) / 3);
  font-size: calc(2.2em / 3);
  font-size: calc(var(--asset-font-size) / 3);
}

.print-page {
  /* paper-css sheet styles */
  margin: 0;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  page-break-after: always;

  /* dimensions */
  width: 8.5in;
  height: 11in;
  padding: 0.25in 0.5in;

  /* my styles */
}

.print-page img {
  max-width: 100%;
  max-height: 100%;
}

/* Hiding everything but what I want */
@media print {
  .app-header,
  .footer,
  .print-controls {
    display: none;
  }
}

/** For screen preview **/
@media screen {
  .print-page {
    background: white;
    box-shadow: 0 0.5mm 2mm rgba(0, 0, 0, 0.3);
    margin: 5mm auto;
  }
}

/** Fix for Chrome issue #273306 **/
@media print {
  .print-page {
    width: 8.5in;
  }
}

